<template>
  <section class="col-group sides">
    <div class="col-6">
      <document-text class="big">Seller:</document-text>
      <space />
      <div class="vertical">
        <document-text>
          <strong>Maciej Mazur Tu i Teraz</strong>
        </document-text>
        <document-text>
          <strong>Perkoza street 15/13</strong>
        </document-text>
        <document-text>
          <strong>44-100, Gliwice, Poland</strong>
        </document-text>
      </div>

      <space />
      <div class="col-group">
        <div class="col-4">
          <document-text>NIP EU</document-text>
        </div>
        <div class="col-8">
          <document-text>
            <strong>1234 4312 4325 5325 6234 PL</strong>
          </document-text>
        </div>
        <div class="col-4">
          <document-text>Bank</document-text>
        </div>
        <div class="col-8">
          <document-text>
            <strong>ING Bank Śląski</strong>
          </document-text>
        </div>
        <div class="col-4">
          <document-text>Bank account</document-text>
        </div>
        <div class="col-8">
          <document-text>
            <strong>5325 6234 6546 1234 4312 4325</strong>
          </document-text>
        </div>
      </div>
    </div>

    <div class="col-6">
      <document-text class="big">Buyer:</document-text>
      <space />
      <div class="vertical">
        <input type="text" v-model="company.line1" />
        <input type="text" v-model="company.line2" />
        <input type="text" v-model="company.line3" />
      </div>

      <space />
      <div class="col-group">
        <div class="col-4">
          <document-text>EU Identifier</document-text>
        </div>
        <div class="col-8">
          <input type="text" v-model="company.id" />
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Sides',
  data: function () {
    return {
      company: {
        line1: 'Some company',
        line2: 'Some street',
        line3: 'Some postcode, city, country',
        id: '1234 1234 1234 1234 1234 EU'
      }
    }
  }
}
</script>

<style scoped>
  .vertical {
    display: flex;
    flex-direction: column;
  }

  input {
    border: 0;
    margin: 0;
    padding: 0;
    display: inline-block;
    width: 100%;
    font-weight: 700;
    font-size: 13pt;
    line-height: 32px;
    font-family: 'Source Sans Pro';
    background-color: transparent;
  }
</style>



