<template>
  <header>
    <document-text class="big">
      <span>Invoice</span>
      <strong>FV 9/2018</strong>
    </document-text>

    <document-text class="small">
      <span>ORIGINAL</span>
    </document-text>
  </header>
</template>

<script>
import moment from 'moment'

export default {
  name: 'Titles',
  props: {
    title: {
      type: String,
      default: () => `FV ${moment().format('M/YYYY')}`
    },
    subtitle: {
      type: String,
      default: 'Original'
    }
  }
}
</script>

<style scoped>
  header {
    display: flex;
    flex-direction: column;
  }

  .small {
    margin-top: 10px;
  }

/*  h2 {
    margin: 0;
  }

  input {
    border: 0;
  }

  h2 input {
    margin: 0;
    font-weight: 300;
    font-size: 22pt;
    letter-spacing: 4px;
    font-family: 'Montserrat';
  }*/
</style>



