import Vue from 'vue'
import Router from 'vue-router'
import Document from '@/components/Document'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Document',
      component: Document,
      props: route => route.query || {}
    }
  ]
})



