<template>
  <section class="subjects">
    <table>
      <tr>
        <th style="width:  6%">No.</th>
        <th>Service name</th>
        <th style="width:  8%">Amount</th>
        <th style="width:  8%">Unit</th>
        <th style="width: 14%">Price netto</th>
        <th style="width: 14%">Value netto</th>
        <th style="width:  6%">%</th>
        <th style="width: 14%">Vat</th>
        <th style="width: 14%">Value brutto</th>
      </tr>

      <tr>
        <td>1.</td>
        <td>Consultation</td>
        <td><input type="number" v-model="amount" /></td>
        <td>hour</td>
        <td><input type="number" v-model="priceNetto" /></td>
        <td>{{ valueNetto | currency }}</td>
        <td><input type="number" v-model="vatPercentage" /></td>
        <td>{{ vat | currency }}</td>
        <td>{{ valueBrutto | currency }}</td>
      </tr>

      <tr>
        <td class="blank" colspan="4"></td>
        <td class="blank">total:</td>
        <td>{{ valueNetto | currency }}</td>
        <td></td>
        <td>{{ vat | currency }}</td>
        <td>{{ valueBrutto | currency }}</td>
      </tr>

      <tr>
        <td class="blank" colspan="4"></td>
        <td class="blank">VAT:</td>
        <td>{{ valueNetto | currency }}</td>
        <td>{{ vatPercentage }}</td>
        <td>{{ vat | currency }}</td>
        <td>{{ valueBrutto | currency }}</td>
      </tr>
    </table>

    <space class="big" />
    <div class="col-group">
      <div class="col-12">
        <document-text class="big">
          <span>Total value:</span>
          <strong v-bind:title="zlotowki | currency(polishCurrency)">{{ valueBrutto | currency }}</strong>
        </document-text>
      </div>

      <div class="col-12">
        <document-text>
          <span style="padding-left: 137px">Verbally :</span>
          <span style="padding-left: 12px">{{ priceVerbally | capitalize }}.</span>
        </document-text>
      </div>
    </div>
  </section>
</template>

<script>
import converter from 'number-to-words'

const getDecimal = split => {
  if (!split) return null
  if (split.length === 1) return split * 10
  return +split
}

export default {
  name: 'Subjects',
  data: function () {
    return {
      vatPercentage: 0,
      amount: 160,
      priceNetto: 10.00,
      polishCurrency: { symbol: 'zł', fractionSeparator: ',' }
    }
  },
  computed: {
    priceVerbally: function () {
      const string = `${this.valueBrutto}`
      const splits = string.split('.')

      const euro = splits[0]
      const cent = getDecimal(splits[1])

      const euroString = `${converter.toWords(euro)} euro`
      const centString = cent > 0 ? ` and ${converter.toWords(cent)} cents` : ''

      return `${euroString}${centString}`
    },
    valueNetto: function () {
      return this.priceNetto * this.amount
    },
    vat: function () {
      return Math.round(this.valueNetto * (this.vatPercentage / 100))
    },
    valueBrutto: function () {
      return this.valueNetto + this.vat
    },
    zlotowki: function () {
      return this.valueBrutto * 4.3
    }
  }
}
</script>

<style scoped>
  table {
    width: 100%;
    border-spacing: 0;
    border-collapse: collapse;
  }

  th {
    background-color: #eee;
    border-bottom: 1px solid #ccc;
  }

  td:not(.blank) {
    font-weight: 700;
    background-color: #faf9f9;
  }

  td.blank {
    background-color: transparent;
    text-align: right;
    opacity: 0.75;
  }

  input,
  th,
  td {
    margin: 0;
    padding: 10px 15px;

    white-space: nowrap;
    text-align: left;
    font-family: monospace;
    font-size: 14px;
    font-weight: normal;
  }

  input {
    border: 0;
    margin: 0;
    padding: 0;
    display: inline-block;
    width: 100%;
    font-weight: 700;
    background-color: transparent;
  }
</style>



